.container-port {
  position: absolute;
  top: 100px;
  bottom: 0px;
  left: 435px;
  right: 0px;
}

.port-name {
  font-weight: 800;
  font-family: "Montserrat";
  font-size: 25px;
  color: #000;
  line-height: 1.2;
  letter-spacing: 0.5;
  margin: 0;
  padding: 0;
}

.port-btn {
  background-color: rgba(0, 0, 0, 0.04);
  text-transform: uppercase;
  padding: 15px 20px;
  font-weight: 600;
  font-size: 13px;
  color: #333;
  font-family: "Montserrat";
  border: none;
}

.container-port .MuiCard-root {
  cursor: pointer;
}
.container-port .MuiCard-root .MuiCardMedia-root {
  transition: 0.4s ease;
}
.container-port .MuiCard-root .MuiCardMedia-root:hover {
  width: 430px;
  height: 450px;
}

.box {
  position: relative;
}

.tooltip {
  position: fixed;
  display: block;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(237, 235, 235, 0.8);
  text-transform: uppercase;
  font-weight: 400 !important;
  font-size: 13px;
  padding: 8px 21px;
  width: 90px;
  color: #222;
  font-family: "Montserrat";
  border: none;
  border: 1px solid transparent;
  border-radius: 10px;
}

.box:hover .tooltip {
  opacity: 0;
  visibility: visible;
  z-index: 10;
}

@media (max-width: 1000px) {
  .container-port {
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    padding: 20px;
  }
}/*# sourceMappingURL=style.css.map */