.about-section{
    position: absolute;
    top: 100px;
	bottom: 0px;
	left: 440px;
	right: 0px;
}

.name-about{
    font-weight: 700;
    font-size: 22px;
	font-family: "Montserrat";
    letter-spacing: 0.5px;
    margin-top: 35px;
}

.self-about{
    font-style: italic;
    font-family: "Montserrat" ;
    color: #767676;
    font-weight: 400;
    line-height: 20px;
    font-size: 15px;
    letter-spacing: 0.5px;
    
}
.right {
    ul{
        list-style-type: none;
        margin: 0;
    }
    span{
        color: #000;
        font-weight: 700;
        font-size: 14px;
        font-family: "Montserrat";
        letter-spacing: 0.5px;
    }
    p{
        color:#767676;
        font-weight: 400;
        font-size: 14px;
        letter-spacing: 0.5px;
        line-height: 30px;
        font-family: "Montserrat";
    }
}

.rm-btn{
    padding: 19px 40px;
    font-size: 15px;
    line-height: 15px;
    border: none;
    cursor: pointer;
    color: #fff;
    background-color: #000;
    font-weight: 500;
    letter-spacing: 0.5px;
    font-family: "Montserrat";
    margin-top: 40px;
}
.img-about{
    width: 700px;
}

@media (max-width:768px){
.about-section{
    top: 0px!important;
    bottom: 0px!important;
    left:0px!important;
    right: 0px!important;
    padding: 20px;
    z-index: -1;

}
.img-about{
    width: 350px!important;
    margin-top: 50px;
}
.self-about{
    width: 300px;
}

}

@media (max-width:1000px){

    .about-section{
        top: 0px!important;
        bottom: 0px;
        left:0px!important;
        right: 0px;
        padding: 20px;
        z-index: -1;
    
    }
    .img-about{
        width: 600px;
        margin-top: 50px;
    }
    .self-about{
        width: 400px;
    }

}